.ready-to-get-started {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1420px;
  height: 439px;
  padding: 70px 125px;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 150px;
  border-radius: 30px;
  background: linear-gradient(
    275deg,
    #403483 0.06%,
    #3585a1 53.1%,
    #27e7c5 99.94%
  );
}
.ready-to-get-started .cyan-circle,
.ready-to-get-started .purple-circle {
  position: absolute;
  bottom: 0;
}
.ready-to-get-started .cyan-circle {
  left: 650px;
}
.ready-to-get-started .purple-circle {
  left: 890px;
}
.ready-to-get-started .main .title {
  display: flex;
  width: 485px;
  height: 104px;
  flex-direction: column;
  justify-content: center;
  color: var(--background, #ece6f6);
  font-family: Poppins;
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px;
  text-transform: capitalize;
  margin-bottom: 20px;
}
.ready-to-get-started .main .body {
  display: flex;
  width: 500px;
  height: 75px;
  flex-direction: column;
  justify-content: center;
  color: var(--background, #ece6f6);
  font-family: Rubik;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 25px;
}
.ready-to-get-started .main .holder {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-top: 30px;
}
.ready-to-get-started .main .holder .circle {
  display: flex;
  width: 70px;
  height: 70px;
  padding: 14px;
  justify-content: center;
  align-items: center;
  border-radius: 35px;
  background: var(--dark-blue, #6f53e1);
}
.ready-to-get-started .main .holder .info {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.ready-to-get-started .main .holder .info span:first-child {
  color: var(--background, #ece6f6);
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 26.4px;
}
.ready-to-get-started .main .holder .info span:last-child {
  color: var(--background, #ece6f6);
  font-family: Rubik;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19.2px;
}
.ready-to-get-started button {
  display: flex;
  width: 203px;
  height: 58px;
  padding: 15px 30px;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 30px;
  background: var(--light-blue, #32cbc8);
  color: var(--background, #ece6f6);
  font-family: Rubik;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 29.25px;
  cursor: pointer;
}
@media (max-width: 767px) {
  .ready-to-get-started {
    width: 100% !important;
    padding: 0 !important;
    border-radius: 0 !important;
    flex-direction: column;
    justify-content: center !important;
    align-items: center;
    gap: 20px;
  }
  .ready-to-get-started .cyan-circle,
  .ready-to-get-started .purple-circle {
    display: none;
  }
  .ready-to-get-started .main .title {
    width: 320px !important;
    font-size: 28px !important;
    text-align: center;
    margin: auto;
  }
  .ready-to-get-started .main .body {
    width: 320px !important;
    text-align: center;
    margin: auto;
  }
}
